.card--casestudy {
    &.card--casestudy--alternative {
        padding: $measure--half;

        .card__anchor {
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;

            @include media('>between') {
                flex-direction: row;
            }

            &,
            &:focus,
            &:active,
            &:visited {
                border: none;
            }

            &:hover {
                @include media('hover') {
                    background-color: transparent;
                    border: none;

                    .card__details__text {
                        background-color: $color-red80;
                    }
                }
            }

            @include media('<=between') {
                background-color: transparent;
            }

            &:after {
                content: '';
                display: none;
            }

            .wrapper--card__thumbnail {
                align-self: center;
                overflow: hidden;

                @include media('>between') {
                    width: 45%;
                }
            }

            .card__thumbnail {
                @include media('<=between') {
                    max-height: 15rem;
                }

                @include media('>between') {
                    margin-bottom: $measure--small2;
                    margin-top: $measure--small2;
                }

                img {
                    display: flex;

                    @include media('<=between') {
                        height: auto;
                        width: 100%;
                    }
                }
            }

            .card__details__text {
                @include box-shadow();

                background-color: $color-red70;
                color: $color-purewhite;
                padding-bottom: $measure--basic;
                transition: background-color $transition-duration ease-in;

                @include media('>between') {
                    width: 55%;
                }

                @include media('reducemotion') {
                    transition: none;
                }

                .card__type {
                    @include extrasmalltype();

                    color: $color-gray2;
                    font-style: italic;
                    margin-bottom: $measure--basic;
                }
            }
        }
    }
}
