.related-content {
    .card--profile {
        .card__details {
            display: flex;
            flex-wrap: nowrap;

            @include media('<small') {
                flex-direction: column;
            }

            @include media('>=small') {
                flex-direction: row;
            }
        }

        .card__thumbnail + .card__details__text {
            @include media('<small') {
                padding-left: 0;
                padding-right: 0;
            }

            @include media('>=small') {
                padding-top: 0;
            }
        }
    }
}

.finder__results {
    .card--profile {
        .card__details {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
        }
    }
}

.card--profile {
    .card__details {
        .card__thumbnail {
            flex-shrink: 0;
            height: $measure--huge5;
            width: $measure--huge5;
        }

        .card__type {
            color: $color-red50;
        }
    }
}
