// <Desktop-specific layout - secondary navigation never present
main > .event {
    @include media('<small') {
        //@include default-margin();
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .event {
            grid-column: 6 / span 14;

            &.event--fullwidth {
                grid-column: 1 / span 24;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .event {
                grid-column: 7 / span 18;
                @include content-grid();

                .event__top__gradient {
                    grid-column: 1 / span 18;
                    @include content-grid();
                }

                .banner-text {
                    grid-column: 1 / span 18;
                }

                .event__info {
                    grid-column: 2 / span 16;
                }

                .content {
                    grid-column: 4 / span 12;
                }
            }
        }
    }
}

.event {
    .content {
        @include media('<small') {
            @include default-margin;
        }
    }
}

@include media('>tablet') {
    .event {
        .banner-text {
            padding-bottom: $measure--medium7;
        }

        .event__info {
            position: relative;
        }
    }
}

.event {
    .event__top__gradient {
        background-image: linear-gradient(
            to top,
            $color-purewhite,
            $color-gray1
        );

        .background--purple80 {
            background-color: $color-purple80;
        }

        .background--pink80 {
            background-color: $color-pink80;
        }

        .background--blue80 {
            background-color: $color-blue80;
        }

        .background--red80 {
            background-color: $color-red80;
        }
    }

    .embedded-video,
    .carousel-gallery-default {
        margin: $measure--threequarters 0;
    }

    .footer-cards {
        grid-column: 1/ -1;
        color: $color-purewhite;

        h2 {
            border-bottom: $border-width-hairline solid $color-red30;
        }

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-gray11,
                $color-outline--dark
            );
        }
    }
}

.event__dates {
    @include smalltype();

    color: $color-gray8;

    span {
        font-weight: $font-weight-medium;
    }
}

.event__info {
    padding: $measure--basic;
    background-color: $color-purewhite;
    margin-bottom: $measure--basic;
    @include box-shadow();

    @include media('<tablet') {
        padding: $measure--basic 0;
        .event__info--wrapper {
            @include default-margin;
        }
    }

    @include media('>=small') {
        grid-column: 2 / span 16;
        .event__info--wrapper {
            display: grid;
            @include x-column-grid(16);
        }
    }

    @include media('>=tablet', '<=small') {
        @include standard-box();
    }

    @include media('>=tablet') {
        margin-top: -$measure--small4;
    }

    .event__info--details__icons {
        margin-right: $measure--half;
        @include smalltype();
        color: $color-red50;
    }
}

.event__info--details {
    grid-column: 1 / span 10;

    @include media('>tablet') {
        display: grid;
        @include x-column-grid(10);

        .event__info--details-top {
            grid-column: 1 / span 6;
        }

        .event__info--details-bottom {
            grid-column: 7 / span 4;
        }
    }

    ul {
        margin-top: 0;
        margin-left: 0;
        list-style: none;

        li {
            display: flex;
            margin-top: 0;
            margin-bottom: $measure--basic;
        }
    }
}

.event__info--cta {
    grid-column: 12 / span 5;
}

.event__info--cta__top {
    @include media('>=small') {
        grid-column: 4/ -1;

        li {
            a {
                width: 100%;
                span {
                    width: 100%;
                    text-align: center;
                }
            }
        }
    }

    @include media('<small') {
        margin: $measure--basic 0;
    }

    a:first-child {
        margin-bottom: $measure--half;
    }

    ul {
        list-style: none;
        margin-top: 0;
        margin-left: 0;
    }

    li {
        margin-top: 0;

        @include media('>mobile', '<small') {
            display: inline;
            margin-right: $measure--basic;
        }
    }
}

.event__info--text {
    grid-column: 1/ -1;
    margin: $measure--half 0;

    @include media('>=small') {
        display: flex;
        justify-content: flex-end;
    }

    .event__info--text__recurring {
        display: inline;

        .fa-redo {
            margin-right: $measure--half;
        }
    }
}

// standfirst

.event_standfirst {
    @include gammatype();
    @include font-family('heading');
    font-weight: $font-weight-light;
}

//event video

.event {
    .embedded-video {
        padding: 0;
    }
}

//event social card

.event__social-cards {
    margin-top: 0;
    margin-left: 0;
    max-width: unset;
}

//event agenda card

.event__agenda {
    list-style-type: none;
    margin-top: 0;
    margin-left: 0;
    max-width: unset;
}

.event__agenda--card {
    background-color: $color-purewhite;
    padding: $measure--basic;
    display: flex;
    @include box-shadow();
}

.event__agenda--card__icon {
    min-width: $measure--medium3;

    span {
        background-color: $color-red50;
        color: $color-purewhite;
        padding: $measure--half;
        @include smalltype();
    }
}

.event__agenda--card__details--time {
    color: $color-gray7;
    @include smalltype();
}

//speaker
.event__speakers {
    list-style-type: none;
    margin-top: 0;
    margin-left: 0;

    li:after {
        content: '';
        display: inline-block;
        width: 100%;
        height: $measure--basic;
        border-bottom: $border-width-default dotted $color-gray7;
    }
}

.event__speakers__name {
    @include gammatype();
}

.event__speakers__name,
.event__speakers__description {
    display: block;
}

.event__speakers__description {
    color: $color-gray7;
}

.event__speakers__details {
    display: flex;
    @include smalltype();
    span {
        @include alphatype();
        margin-right: $measure--half;
    }
}

//sponsor

.event__sponsors {
    margin-top: 0;
    margin-left: 0;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    justify-content: space-between;

    li {
        margin-top: $measure--half;
        width: 40%;
    }
    @include linked-img();
}

//event tags

.event__tags {
    h2 {
        font-family: $font-family-plex-sans;
        color: $color-gray7;
        margin-bottom: $measure--basic;
        @include smalltype();
    }

    .cta-block__cta {
        margin: 0 $measure--small2 $measure--threequarters 0;
    }
}

//event guide

.event__guide {
    margin: $measure--basic 0;
}

.event_related-events {
    grid-column: 1/ -1;
    background-color: $color-gray1;

    .event_related-events__wrapper {
        padding: $measure--basic 0;
    }

    h2 {
        text-align: center;
    }

    .related-content {
        flex-wrap: wrap;
        margin-top: $measure--small4;
        margin-bottom: $measure--small4;

        @include media('>=small') {
            @include standard-box();
        }

        @include media('<small') {
            @include default-margin;
        }

        @include media('>mobile') {
            display: flex;
            justify-content: space-between;
        }
    }

    li {
        @include media('>small') {
            width: 32%;
        }
        @include media('>=mobile', '<=small') {
            width: 47%;
        }
    }
}

//contact details

.event__contact-details {
    grid-column: 1/ -1;
    padding: $measure--basic $measure--basic $measure--small4;
    background-color: $color-red70;
    color: $color-purewhite;

    h2 {
        color: $color-purewhite;
        border-bottom: $border-width-default solid $color-purewhite;
    }

    .icon {
        margin-right: $measure--basic;
    }

    a {
        @include anchor-inline(
            $color-gray4,
            $color-gray4,
            $color-gray11,
            $color-outline--dark
        );
    }
}
