////
/// Slider
/// Navigate to previous/next content in listing (arrows/dots)
/// @group patterns
////

.slider-block__control {
    display: flex;
    justify-content: flex-end;
    padding: $measure--basic 0;

    &[data-control-type='dot'] {
        @include media('>tablet') {
            padding: 0;
        }
    }
}

.slider-block__control__progress {
    margin: $measure--basic $measure--small4 0 0;
    display: inline-block;

    span {
        color: $color-red50;
        font-size: $font-size--extrasmalltype;
        font-weight: $font-weight-book;
        display: inline-block;
    }

    span:nth-of-type(1),
    span:nth-of-type(2) {
        color: $color-gray7;
        margin-right: $border-width-thick;
    }
}

.slider-block__control__buttons {
    display: inline-block;
}

.slider-block__control__buttons--default {
    button {
        @include control-button();
    }

    button[class*='prev--'] {
        margin-right: $measure--basic;
    }
}

.slider-block__control__buttons--dot {
    display: flex;
    margin: 0 auto;

    button {
        align-self: center;
        display: inline-block;
        position: relative;
        border-radius: 50%;
        width: $measure--small1;
        height: $measure--small1;
        margin: $measure--basic;
        background-color: $color-gray7;
        cursor: pointer;
        transition: all $transition-duration ease;

        &:before {
            content: '';
            position: absolute;
            left: -0.25rem;
            top: -0.23rem;
            bottom: 0;
            transition: all $transition-duration ease;
            border-radius: 50%;
            width: $measure--small3;
            height: $measure--small3;
            border: 0.15rem dotted transparent;
        }

        &:hover {
            @include media('hover') {
                background-color: $color-gray10;
            }
        }

        &:active,
        &:focus {
            background-color: $color-gray10;
            &:before {
                border: $border-width-default $border-style-dotted $color-gray10;
            }
        }
    }

    button[aria-expanded='true'] {
        background-color: $color-gray10;
        width: $measure--small2;
        height: $measure--small2;
        cursor: default;

        &:before {
            border: 0;
        }

        &:hover {
            @include media('hover') {
                box-shadow: none;
                border: none;
            }
        }

        &:active,
        &:focus {
            box-shadow: none;
            border: none;

            &:before {
                border: 0;
            }
        }
    }
}

.slider-block {
    [data-visible='false'] {
        display: none;
    }

    [data-control='dot'] > * {
        &:focus {
            outline-color: $color-outline;
            outline-offset: $border-width-thick;
            outline-style: $border-style-dotted;
            outline-width: $border-width-default;
        }
    }
}

.slider-block--mobile {
    [data-visible='false'] {
        display: block;
    }

    .slider-block__control__buttons--dot {
        display: none;
    }

    @include media('<=mobile') {
        [data-visible='false'] {
            display: none;
        }

        .slider-block__control__buttons--dot {
            display: block;
        }
    }
}

.slider-block--responsive {
    .slider {
        @include media('<=mobile') {
            [data-visible] {
                display: none;
            }

            [data-visibletablet] {
                display: none;
            }

            [data-visibledesk] {
                display: none;
            }

            [data-visible='true'] {
                display: inline-block;
            }
        }

        @include media('>mobile', '<=tablet') {
            [data-visible] {
                display: none;
            }

            [data-visibletablet] {
                display: none;
            }

            [data-visibledesk] {
                display: none;
            }

            [data-visibletablet='true'] {
                display: inline-block;
            }
        }

        @include media('>tablet') {
            [data-visible] {
                display: none;
            }

            [data-visibletablet] {
                display: none;
            }

            [data-visibledesk] {
                display: none;
            }

            [data-visibledesk='true'] {
                display: inline-block;
            }
        }
    }

    .slider-block__control__progress--default {
        display: none;
        @include media('<=mobile') {
            display: block;
        }
    }

    .slider-block__control__progress--tablet {
        display: none;
        @include media('>mobile', '<=tablet') {
            display: block;
        }
    }

    .slider-block__control__progress--desk {
        display: none;
        @include media('>tablet') {
            display: block;
        }
    }

    .slider-block__control__buttons {
        button {
            display: none;
        }

        @include media('<=mobile') {
            button[data-visible] {
                display: inline-block;
            }
        }

        @include media('>mobile', '<=tablet') {
            button[data-visibletablet] {
                display: inline-block;
            }
        }

        @include media('>tablet') {
            button[data-visibledesk] {
                display: inline-block;
            }
        }
    }
}
