.wrapper--tooltip__label {
    display: flex;
    position: absolute;
    right: 0;
    top: 0;
    background-color: $color-gray10;
    z-index: z-index('above');
    @include smalltype;
    &[data-selected='false'] {
        .tooltip__label {
            display: none;
        }
    }

    .tooltip__label {
        padding: $measure--half;
        color: $color-purewhite;
        a {
            @include anchor-discreet--notransition(
                $color-purewhite,
                $color-gray3,
                $color-purewhite,
                $color-purewhite,
                $color-purewhite
            );
        }
    }

    button.tooltip--image-credit {
        border: 0;
        color: $color-purewhite;
        font-weight: $font-weight-normal;
        padding: $measure--half;
        margin: 0;
        background-color: $color-gray10;
        width: auto;
        @include smalltype;

        &:hover {
            @include media('hover') {
                background-color: $color-gray8;
            }
        }

        &:focus-within {
            .tooltip__icon {
                outline: $border-width-default $border-style-dotted
                    $color-purewhite;
            }
        }

        .tooltip__icon {
            padding: $measure--half;
        }
    }
}
